body {
  background-image: linear-gradient(to right, #eea2a2 0%, #bbc1bf 19%, #57c6e1 42%, #b49fda 79%, #7ac5d8 100%);
}

table {
  border-collapse: collapse;
  color: #fff;
  background-color: rgba(62, 171, 255, .6);
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

table th, table td {
  border: 2px solid #fff;
  padding: 10px;
}

.title {
  color: #fff;
  justify-content: center;
  display: flex;
}

.container {
  background-color: rgba(255, 255, 255, .4);
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 2px 1px 3px rgba(0, 0, 0, .3);
}

.container-players {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.table {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
}

.btn {
  color: #fff;
  cursor: pointer;
  background-color: rgba(62, 171, 255, .6);
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 22px;
  transition: all .3s ease-in-out;
  position: relative;
}

.btn:hover {
  background-color: #3eabff;
}

.btn:focus {
  background-color: #3eabff;
  outline: 3px solid rgba(255, 255, 255, .8);
  box-shadow: 0 0 0 6px rgba(62, 171, 255, .8);
}

.btn:disabled {
  cursor: not-allowed;
  background-color: #777;
}

.addPlayer-form_input {
  width: 400px;
  color: #777;
  background-color: rgba(255, 255, 255, .6);
  border: none;
  border-radius: 5px;
  margin-right: 20px;
  padding: 10px 20px;
  font-size: 22px;
  transition: all .35s ease-in-out;
}

.players {
  color: #fff;
  background-color: rgba(62, 171, 255, .8);
  border: 2px solid #fff;
  border-radius: 10px;
  padding: 10px;
}

